import React, { useState, useEffect, useRef } from "react";
import {
  AppBar,
  Toolbar,
  IconButton,
  List,
  ListItem,
  ListItemText,
  Drawer,
  Button,
  Box,
  useTheme,
  useMediaQuery
} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import { Link as ScrollLink, scroller, Events, scrollSpy } from "react-scroll";
import "./WeddingInvite.css";

const Navbar = () => {
  const [open, setOpen] = useState(false); // State for Drawer
  const [activeLink, setActiveLink] = useState("invite");
  const navLinks = ["invite", "event", "timeline", "albumn", "qr"];
  const navTexts = ["Li mi", "Tic ci", "Lch trnh", "nh ci", "Mng ci"];
  const sections = ["invite", "event", "timeline", "albumn", "qr"];
  const isScrollingRef = useRef(false); // Track if a scroll animation is in progress
  const touchStartY = useRef(0); // Store the initial touch position for vertical swipe
  const touchStartX = useRef(0); // Store the initial touch position for horizontal swipe
  const theme = useTheme();


  useEffect(() => {
    
    // Registering the 'begin' event and logging it to the console when triggered.
    Events.scrollEvent.register('begin', (to, element) => {
      console.log('begin', to, element);
      isScrollingRef.current = true
    });

    // Registering the 'end' event and logging it to the console when triggered.
    Events.scrollEvent.register('end', (to, element) => {
      console.log('end', to, element);
      isScrollingRef.current = false
    });

    scrollSpy.update();

    // Returning a cleanup function to remove the registered events when the component unmounts.
    return () => {
      Events.scrollEvent.remove('begin');
      Events.scrollEvent.remove('end');
    };
  }, []);

  // Navigate to the next or previous section
  const navigateSection = (direction) => {
    if (isScrollingRef.current) return; // Block if a scroll animation is in progress

    const currentIndex = sections.indexOf(activeLink);
    let nextSection = currentIndex + direction;

    // Ensure nextSection stays within bounds
    if (nextSection < 0) nextSection = 0;
    if (nextSection >= sections.length) nextSection = sections.length - 1;

    if (sections[nextSection] !== activeLink) {
      scroller.scrollTo(sections[nextSection], {
        duration: 600, // Animation duration
        smooth: true, // Enable smooth scrolling
        ignoreCancelEvents: true, // Ignore cancel events
      });
      setActiveLink(sections[nextSection]);
    }
  };

  // Handle wheel (scroll) events for vertical scrolling
  const handleScroll = (event) => {
    if (isScrollingRef.current) {
      event.preventDefault(); // Prevent default scroll behavior if scrolling is in progress
      return;
    }

    if (Math.abs(event.deltaY) > Math.abs(event.deltaX) && Math.abs(event.deltaY) > 50) {
      event.preventDefault(); // Prevent default vertical scroll behavior
      const delta = Math.sign(event.deltaY); // Get vertical scroll direction
      navigateSection(delta);
    }
    // Allow horizontal scrolling by not preventing default behavior for deltaX
  };

  // Handle touch start event
  const handleTouchStart = (event) => {
    touchStartY.current = event.touches[0].clientY; // Store the initial touch position for vertical swipe
    touchStartX.current = event.touches[0].clientX; // Store the initial touch position for horizontal swipe
  };

  // Handle touch move event
  const handleTouchMove = (event) => {
    if (isScrollingRef.current) {
      event.preventDefault(); // Prevent default scroll behavior if scrolling is in progress
      return;
    }

    const touchEndY = event.touches[0].clientY; // Get the current touch position for vertical swipe
    const touchEndX = event.touches[0].clientX; // Get the current touch position for horizontal swipe
    const deltaY = touchStartY.current - touchEndY; // Calculate the vertical swipe direction
    const deltaX = touchStartX.current - touchEndX; // Calculate the horizontal swipe direction

    // Check if the swipe is primarily vertical or horizontal
    if (Math.abs(deltaY) > Math.abs(deltaX)) {
      if (Math.abs(deltaY) > 0) { // Add a threshold to avoid accidental swipes
        event.preventDefault(); // Prevent default vertical scroll behavior
        navigateSection(Math.sign(deltaY));
      }
    }
    // Allow horizontal scrolling by not preventing default behavior for horizontal swipes
  };

  // Handle keydown events for arrow keys
  const handleKeyDown = (event) => {
    if (isScrollingRef.current) return; // Block if a scroll animation is in progress

    if (event.key === 'ArrowDown') {
      navigateSection(1); // Slide down
    } else if (event.key === 'ArrowUp') {
      navigateSection(-1); // Slide up
    }
  };

  // Add event listeners for wheel, touch, and keydown events
  useEffect(() => {
    // const container = document.getElementById('invitation-main'); // Add an ID to your container
    // container.addEventListener('wheel', handleScroll, { passive: false });
    // container.addEventListener('touchstart', handleTouchStart, { passive: false });
    // container.addEventListener('touchmove', handleTouchMove, { passive: false });
    window.addEventListener('keydown', handleKeyDown);

    return () => {
      // container.removeEventListener('wheel', handleScroll);
      // container.removeEventListener('touchstart', handleTouchStart);
      // container.removeEventListener('touchmove', handleTouchMove);
      window.removeEventListener('keydown', handleKeyDown);
    };
  }, [activeLink]);

  // Handle active section on scroll
  const handleSetActive = (section) => {
    setActiveLink(section); // Set the active section
  };

  return (
    <>
      {/* Transparent Navbar with White Background and Top Margin */}
      <AppBar
        position="fixed"
        elevation={0} // Removes shadow
        sx={{
          backgroundColor: "rgba(255, 255, 255, 0)", // Transparent white
          backdropFilter: "blur(10px)", // Blurry effect
          color: "black",
          boxShadow: "none",
          marginTop: {xs: "0px", md: "30px"}, // Top margin
          // minHeight: {xs: "50px", md: "100px"}
        }}
      >
        <Toolbar
          sx={{
            width: "100%",
            maxWidth: "1200px", // Center align content within max width
            margin: "0 auto",
            display: "flex",
            justifyContent: "center", // Center align navbar items
            alignItems: "center",
            gap: 4,
          }}
        >
          {/* Mobile Menu Icon (Visible only on Small Screens) */}
          <IconButton
            sx={{ display: { xs: "block", md: "none" }, color: "black", position: "absolute", left: 16 }}
            onClick={() => setOpen(true)}
          >
            <MenuIcon />
          </IconButton>

          {/* Desktop Menu (Hidden on Small Screens) */}
          <Box sx={{ display: { xs: "none", md: "flex" }, gap: 4 }}>
            {navLinks.map((text, index) => (
              <ScrollLink
                key={text}
                to={text.toLowerCase()} // Scroll to respective section
                spy={true} // Automatically highlight the section in view
                smooth={true} // Smooth scroll animation
                // hashSpy={true}
                duration={600} // Duration of scroll animation
                offset={0} // Offset for navbar height (if needed)
                onSetActive={handleSetActive}
                ignoreCancelEvents={true}
                spyThrottle={10}
              >
                <Button
                  sx={{
                    color: "#6B564D",
                    fontSize: {xs:"16px", md:"25px"},
                    fontWeight: "600",
                    fontFamily: 'Cormorant Infant',
                    textTransform: "none",
                    borderRadius: "30px", // Rounded corners
                    border: activeLink === text.toLowerCase() ? "2px solid #b8a06c" : "2px solid transparent", // Border only when active
                    // "&:hover": {
                    //   border: "2px solid yellow", // Show border on hover
                    // },
                }}>
                  {navTexts[index]}
                </Button>
              </ScrollLink>
            ))}
          </Box>
        </Toolbar>
        {/* Divider below AppBar */}
        {/* <Divider sx={{backgroundColor: "black", height: "2px", marginLeft: "15%", marginRight: "15%", display: { xs: "none", md: "block" }}} /> */}
      </AppBar>

      {/* Drawer for Mobile Menu */}
      <Drawer anchor="left" open={open} onClose={() => setOpen(false)}>
        <List sx={{ width: 250 }}>
          {navLinks.map((text, index) => (
            <ScrollLink
                key={text}
                to={text.toLowerCase()} // Scroll to respective section
                spy={true} // Automatically highlight the section in view
                smooth={true} // Smooth scroll animation
                // hashSpy={true}
                duration={600} // Duration of scroll animation
                offset={0} // Offset for navbar height (if needed)
                onSetActive={handleSetActive}
                ignoreCancelEvents={true}
                spyThrottle={10}
            >
            <ListItem button 
              key={text}
              onClick={() => setOpen(false)}
              sx={{ mb: 2 }}
            >
              <ListItemText 
                primary={
                  <span 
                    style={{
                      fontFamily: 'Cormorant Infant',
                      fontSize: "20px",
                      fontWeight: "bold"
                    }}>
                      {navTexts[index]}
                  </span>
                }
                sx={{
                  color: "#6B564D",
                  fontWeight: {xs:"bold", md:"500"},
                  textDecoration: activeLink === text.toLowerCase() ? "underline" : "none", // Highlight active link
                  fontFamily: 'Cormorant Infant',
                }}
                />
            </ListItem>
            </ScrollLink>
          ))}
        </List>
      </Drawer>
    </>
  );
};

export default Navbar;
