import React, { useState } from "react";

import { Box, ImageList, ImageListItem, useMediaQuery, Modal, useTheme } from '@mui/material';
import img1 from "./assets/TTPG0147.JPG"
import img2 from "./assets/UYPW0087.JPG"
import img3 from "./assets/UYPW9839.JPG"
import img4 from "./assets/UYPW0526.JPG"
import img5 from "./assets/UYPW0364.JPG"
import img6 from "./assets/UYPW0956.JPG"
import img7 from "./assets/TTPG0177.JPG"
import img8 from "./assets/UYPW0938.JPG"


export default function MasonryImageList() {
    const theme = useTheme();
    const isMobile = useMediaQuery(theme.breakpoints.down('md'));

    const [selectedImage, setSelectedImage] = useState(null);

    const handleImageClick = (img) => {
      setSelectedImage(img);
    };
  
    const handleClose = () => {
      setSelectedImage(null);
    };
  
    return (
      <Box
        id="image-gallery"
        sx={{
          maxHeight: { xs: "100vh", md: "80vh" },
          width: "100%",
          maxWidth: "1200px",
          margin: "0 auto",
        //   padding: 2,
          overflow: isMobile ? "scroll" : "scroll",
          display: isMobile ? "flex" : "block", // Flex for horizontal scroll on mobile
          gap: isMobile ? 2 : 0, // Add spacing in flex mode
          whiteSpace: isMobile ? "nowrap" : "normal", // Prevents wrapping in flex mode
          scrollbarWidth: "none", // Hides scrollbar (Firefox)
          "&::-webkit-scrollbar": { display: "none" }, // Hides scrollbar (Chrome, Safari)
          overflowX: "scroll"
        }}
      >
        {isMobile ? (
          // Horizontal scrolling list for small screens
          itemData.map((item) => (
            <Box key={item.img} sx={{ flex: "0 0 auto", width: "70%", maxWidth: "300px" }} onClick={() => handleImageClick(item.img)}>
              <img
                src={`${item.img}?w=600&fit=crop&auto=format`}
                loading="lazy"
                style={{
                  width: "100%",
                  height: "auto",
                  minHeight: "65vh",
                  borderRadius: "8px",
                  objectFit: "cover",
                }}
              />
            </Box>
          ))
        ) : (
          // Masonry layout for large screens
          <ImageList variant="masonry" cols={4} row={2} gap={12}>
            {itemData.map((item) => (
              <ImageListItem key={item.img} onClick={() => handleImageClick(item.img)}>
                <img
                  src={`${item.img}?w=600&fit=crop&auto=format`}
                  loading="lazy"
                  style={{
                    width: "100%",
                    height: "auto",
                    maxHeight: "38vh",
                    borderRadius: "8px",
                    objectFit: "cover",
                  }}
                />
              </ImageListItem>
            ))}
          </ImageList>
        )}
        <Modal open={!!selectedImage} onClick={handleClose} onClose={handleClose}>
          <Box
            sx={{
              position: "absolute",
              top: "50%",
              left: "50%",
              transform: "translate(-50%, -50%)", // Centers the modal
              width: { xs: "100vw", sm: "auto" }, // Full width on small screens
              maxWidth: "90vw", // Prevents overflow
              maxHeight: "90vh", // Ensures it fits on screen
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              outline: "none",
              bgcolor: "rgba(0, 0, 0, 0.8)", // Optional: Dark overlay
            }}
          >
            {selectedImage && (
              <img
                src={selectedImage}
                alt="Preview"
                style={{
                  width: "100%", // Makes the image fully responsive
                  height: "auto",
                  maxHeight: "90vh", // Prevents it from overflowing vertically
                  objectFit: "contain", // Maintains aspect ratio
                }}
              />
            )}
          </Box>
        </Modal>
      </Box>
    );
  }

const itemData = [
  {img: img1},
  {img: img5},
  {img: img2},
  {img: img4},
  {img: img3},
  {img: img6},
  {img: img7},
  {img: img8}
];


